import React, {
  useContext,
  useEffect,
  useLayoutEffect,
  useMemo,
  useState,
} from "react";
import "../styles/words.css";
import { ApiConext } from "../axios/AxiosContenxt";
import { getDash } from "../tools/dash";
import { getRandom } from "../tools/random";
import { shuffleArray } from "../tools/shuffle";
import Wheel from "./Wheel";
import useCounDownTimer from "../tools/timer";
import { getAllCombinations } from "../tools/new";
import { ScaleLoader } from "react-spinners";
const Words = ({ pass, value }) => {
  const { words, isLoading, setIsLoading, trigger, setTrigger } =
    useContext(ApiConext);
  const [gen, setGen] = useState(false);
  const [filt, setFilt] = useState([]);
  const [load, setLoad] = useState(false);
  const [printDash, setPrintDash] = useState([]);
  const [collect, setCollect] = useState([]);
  const [time, setTime] = useState(0);
  const [shuff, setShuff] = useState([]);
  const [newShuff, setNewShuff] = useState([]);
  const [resetTimer, setResetTimer] = useState(false);
  const [lett, setLett] = useState([]);
  // const [duration, setDuration] = useState(10)
  const duration = 180;
  const timeLeft = useCounDownTimer(duration, setGen, resetTimer);
  const minutes = Math.floor(timeLeft / 60);
  const seconds = timeLeft % 60;
  const secDuration = 30;
  const secondLeft = useCounDownTimer(secDuration, resetTimer);
  const [resetSec, setResetSec] = useState(false);
  const [longestLenght, setLongestLenght] = useState();
  const [newSet, setNewSet] = useState([]);
  const [isLoadWord, setIsLoadWord] = useState(true);

  useEffect(() => {
    setIsLoadWord(true);
    isLoadWord && setFilt([]);
    isLoadWord && setPrintDash([]);
    isLoadWord && setCollect([]);

    setShuff(words[1]);
    setIsLoadWord(true);

    setCollect([]);
  }, [isLoading, trigger]);

  useEffect(() => {
    words[0] && setFilt(words[0]);
  }, [isLoading, trigger]);
  useEffect(() => {
    filt.length !== 0 && getDash(filt, setPrintDash);
    filt.length !== 0 && setIsLoadWord(false);
    const longest = filt?.reduce((acc, curr) => {
      return curr.length >= acc.length ? curr : acc;
    }, "");
    filt && setLongestLenght(longest.length - 1);
  }, [filt]);

  useEffect(() => {
    let check = value[value.length - 1];
    let filtered = check?.toLowerCase();
    let gessted = filt?.includes(filtered?.toLowerCase());
    if (gessted) {
      if (!collect.includes(filtered)) {
        setCollect((prev) => [...prev, filtered]);
      }
    }
  }, [filt, pass, value]);

  useEffect(() => {
    if (collect.length === filt.length && filt.length !== 0) {
      const timeOut = setTimeout(() => {
        setIsLoadWord(true);
        setTrigger(!trigger);
        setCollect([]);

        setResetTimer(!resetTimer);
        setTime(0);
        setResetSec(!resetSec);
        setIsLoading(true);
      }, 4000);
      return () => clearTimeout(timeOut);
    }
  }, [time, collect]);

  useEffect(() => {
    if (timeLeft === 0) {
      setLongestLenght((prev) => longestLenght - 1);
      setTime((prev) => time + 1);
    }
  }, [timeLeft, longestLenght]);


  useEffect(() => {
    const int = setInterval(() => {
      let temp = shuffleArray(shuff);
      setShuff(temp);
      setResetSec(!resetSec);
    }, 30000);
    return () => clearInterval(int);
  }, [resetSec, shuff]);

  return isLoadWord ? (
    <div className="spinner">
      <ScaleLoader height={"150px"} width={"10px"} color="#a14444" />
    </div>
  ) : (
    <div className="words">
      {filt?.map((item, i) => {
        return (
          <div key={i}>
            <h2 className="the-word">
              {collect.includes(filt[i])
                ? filt[i]
                : filt[i]?.substring(0, time)}
            </h2>
          </div>
        );
      })}
      <div className="pos-dash">
        {printDash?.map((set, i) => {
          return (
            <div className="all-dash" key={i}>
              {set.map((item, s) => {
                return (
                  <span key={s} className="single-dash">
                    {item}
                  </span>
                );
              })}
            </div>
          );
        })}
      </div>
      {!isLoadWord && (
        <div className="wheel">
          <h4>–í—ä–∑–º–æ–∂–Ω–æ –µ –±—É–∫–≤–∏—Ç–µ –¥–∞ —Å–µ –ø–æ–≤—Ç–∞—Ä—è—Ç üòÅ</h4>
          <h5 className="seconds">
            {secondLeft < 10 ? "0" + secondLeft : secondLeft}
          </h5>
          {shuff?.map((item, i) => {
            return (
              <div key={i} className={`item i-${i}`}>
                <span>{item}</span>
              </div>
            );
          })}
        </div>
      )}
      <div className="counter">
        {" "}
        <h4>
          {minutes} : {seconds < 10 ? "0" : ""}
          {seconds}
        </h4>
      </div>
      {longestLenght < 0 && (
        <div className="next">
          <button
            className="btn-next"
            onClick={() => {
              setIsLoadWord(true);
              // // setGen(!gen);
              setIsLoading(true);
              setTrigger(!trigger);
              setCollect([]);
              setResetTimer(!resetTimer);
              setResetSec(!resetSec);
              setTime(0);
            }}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};

export default Words;
