import { useState } from 'react';

const words = ['–¥—Ä–µ—Ö–∞', '–≥–ª—ä—Ç–∫–∞', '–¥—ä–≤—á–∞', '–∑–∞–±–æ–¥–∞', '–∑–∞–ª–æ–≤—è', '–≤—Ä–µ–¥'];

function Wheel() {
    const [showFullWords, setShowFullWords] = useState(false);
  
    // Create a new array with only the first and last letters of each word
    const abbreviatedWords = words.map(word => {
      const firstLetter = word.charAt(0);
      const lastLetter = word.charAt(word.length - 1);
      const middleLetters = word.slice(1, -1).replace(/./g, ' ');
      return `${firstLetter}${middleLetters}${lastLetter}`;
    });
    
    return (
      <div>
        <button onClick={() => setShowFullWords(!showFullWords)}>
          {showFullWords ? 'Hide full words' : 'Show full words'}
        </button>
        {showFullWords ? (
          <ul>
            {words.map((word, index) => (
              <li key={index}>{word}</li>
            ))}
          </ul>
        ) : (
          <ul>
            {abbreviatedWords.map((word, index) => (
              <li key={index}>{word}</li>
            ))}
          </ul>
        )}
      </div>
    );
}

export default Wheel;