export const getDash =  (filt, setPrintDash) =>  {
    if (!filt) {
      return;
    }
    const dashCount = filt?.map((word) => word?.length);
    let dashHold = dashCount?.map((count) => {
      let pushed = [];
      const str = "";
      for (let j = count - 1; j >= 0; j--) {
        pushed?.push(str);
      }
      return pushed;
    });
     setPrintDash(dashHold);
  };